
.card {
  cursor: pointer;
  background-color: transparent;
  height: 300px;
  perspective: 1000px;
}

.card h1 {
  font-size: 25px;
  border-bottom: 1px #fff solid;
  padding-bottom: 10px;
  margin-bottom: 10px;
}

.card img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  position: relative;
  border: 2px solid #29332900;
}
.card-text{
    position: absolute;
    bottom: 3px;
    width: 100%;
    color: white;
    text-align: center;
    font-family: var(--font);
    font-size: 1.5rem;
    background-color: #173911a8;
    border-radius: 4px;
}
.card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.card:hover .card-inner {
  transform: rotateY(180deg);
}

.card-front,
.card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.card-back {
  background-color: #333;
  color: white;
  padding: 20px;
  transform: rotateY(180deg);
  outline: 4px solid #4aa54a;

}

.card li {
  list-style: none;
  padding-bottom: 10px;
}